.loader{
border-radius: 50%;
border: 3px dashed teal;
width:100px;
height:100px;
animation: rotate 1s infinite linear;
}
@keyframes rotate {
  0%{
  transform: rotate(0deg);
  scale:1;
  }
  50%{
  transform: rotate(180deg);
  scale:0
  }
  100%{
  transform: rotate(360deg);
  scale:1
  }
}